<!-- 商品回复组件 -->
<template>
	<div>
		<div class="mt-1 container" v-show="replybooleans" v-for="commentreplyobj,index in commodityCommentRepliesObj" :key="commentreplyobj">
			<div class="d-flex align-items-center">
					<img src="../../../../assets/imgs/1/mantrousers1.jpg" width="24px" height="24px" style="border-radius: 50%;" >
					<span class="ml-2 " style="font-size: 10.5px;color: #606266; text-align: left;">{{commentreplyobj.user.username}}
					<span v-if="commentreplyobj.commodityCommentReply!=null" ><i class="el-icon-caret-right"></i>
					{{commentreplyobj.commodityCommentReply.user.username}}</span></span>
			</div>
			<div style="font-size: 12px;">
					<div class="ml-3 col-8" style="text-align: left;">
						{{commentreplyobj.commentreply}}
					</div>
					<div class="ml-3 col-8" style="font-size: 10px;">
						<span style="color: #606266;">2022-5-22</span>
						<template v-if="user!=''">
							<i class="el-icon-delete ml-1" @click="delComentReply(commentreplyobj.replyid)" v-if="user.id==commentreplyobj.userid"></i>
							<i class="el-icon-chat-dot-round ml-1" @click="commentbooleans[index].commentbooleanstring=!commentbooleans[index].commentbooleanstring"></i>
				</template>
					</div>
					<!-- 商品评论模板 -->
						<el-drawer
							:visible.sync="commentbooleans[index].commentbooleanstring"
							direction="btt">
							<div class="container">
							<el-input 
						type="textarea"
							:placeholder="'回复@'+commentreplyobj.user.username"
						v-model="textComment"
						maxlength="200"
						autosize />
							</div>
							<div class=" mt-2 mb-2 d-flex justify-content-around container">
								<el-button type="danger" @click="commentbooleans[index].commentbooleanstring=false,textComment=''" round>取消</el-button>
								<el-button type="danger" 
								@click="addCommodityCommentReply(commentreplyobj.commoditycommentidoneid,commentreplyobj.replyid)" round>确认</el-button>
							</div>
							</el-drawer>
			</div>
			
		</div>
		<span @click="replybooleans=!replybooleans"
		style="font-size: 10px; color: #82848A; cursor: pointer;">
		<span v-if="replybooleans==false">展开{{commodityCommentRepliesObj.length}}条回复<i class="el-icon-arrow-down"></i></span>
		<span v-if="replybooleans==true">收起回复<i class="el-icon-arrow-up"></i></span>
		</span>
	</div>
</template>

<script>
	import {Ajax} from "../../../../assets/js/ajax.js"
	import bus from '../../../../main.js'
export default{
	name:"CommodityCommentReply",
	data:function(){
		return{
			commentbooleans:'false',
			textComment:'',
			user:''
		}
	},
	props:{
		commodityCommentRepliesObj:Array,
		replybooleans:Boolean,
		index:Number
	},
	created:function(){
		//查询当前用户
		Ajax("get","user/homePageUser",null,(data)=>{
			this.user=data
		})
		//解决id覆盖问题
		this.commentbooleans='';
		this.commentbooleans=[];
		for(var i=0;i<this.commodityCommentRepliesObj.length;i++){
			this.commentbooleans.push({"commentbooleanstring":false})
		}
	},
	methods:{
		//添加评论
		addCommodityCommentReply:function(commoditycommentidoneid,commoditycommentidreplyid){
			var data="userid="+this.user.id+"&commentreply="
			+this.textComment+"&commoditycommentidoneid="+commoditycommentidoneid+"&commoditycommentidreplyid="+commoditycommentidreplyid;
			Ajax("post","commodityCommentReply/addCommodityCommentReply",data,(dataResult)=>{
				if(dataResult.success==true){
					this.textComment='';//评论框清空
					alert(dataResult.message)
					bus.$emit('commodityComment',this.index);//发送到父评论组件中
				}else{
					alert(dataResult.message)
				}
				})
		},
		//删除回复的评论
		delComentReply:function(replyid){
			this.$confirm('此评论将永久删除, 是否继续',{
			confirmButtonText:'确定',
			cancelButtonText:'取消',
				type: 'warning'
			}).then(() => {
				var dataReplyid="replyid="+replyid;
				Ajax("post","commodityCommentReply/delComentReply",dataReplyid,(dataResult)=>{
					if(dataResult.success==true){
						this.$message({
							type: 'success',
							message:dataResult.message
						});
						bus.$emit('commodityComment',this.index);//发送到父评论组件中
					}else{
						alert(dataResult.message)
					}
					})
			}).catch(() => {
			this.$message({
			type: 'info',
			message: '已取消删除'
			});          
			});
		}
	},
}
</script>
<style>
</style>
