<template>
	<div class="text-left container">
	<div style="font-weight: bold;" class="mt-2 mb-2 ml-1 commodityOnetitle">
								{{commodity.commodityname}}
								</div>	
		<!-- 价格 -->
							<div style="background-color: white;" class="commodityinfoprice" >
								<div>价格：
									<span style="font-size: 20px; font-weight: bold; color: #FF0000;">￥{{commodity.commodityprice}}</span>
								</div>
								<div style="margin-bottom: 10px; margin-top:0;">本店活动：
									<span style="font-size: 13px; color: chocolate;">本店活动已结束喽!
									</span>
								<el-dropdown trigger="click" style="margin-left: 8rem;">
								<span class="el-dropdown-link" style="font-size: 12px; color: chocolate; ">
								优惠详情<i class="el-icon-arrow-down el-icon--right"></i>
								</span>
								<el-dropdown-menu slot="dropdown">
											<div style="width: 360px;">
												<div style="font-size: 12px;text-indent: 1em; border-bottom: 1px dotted #A6A9AD;">活动日期：</div>
												<ul>
													<li>123</li>
													<li>123</li>
													<li>123</li>
												</ul>
											</div>
								</el-dropdown-menu>
								</el-dropdown>
								</div>
							</div>
							
				<!-- 商品评价和销量 -->
						<div class=" d-flex justify-content-around align-items-center commodityinfo_sales_pj">
							<div>
								月销量<i> 200+</i>
							</div>
							<div>
								累计评论
								<i style="cursor: pointer;" @click="skipcomment"> 200+</i>
							</div>
						</div>
						
						<!-- 选者商品类型 -->
						<div class="selectcommodityfuji">
						
							<div class="selectcommodity" v-for="(obj,index) in specificationTitle" :key="obj">
									<div class="selectcommodityfont">{{obj.specificationtitle}}</div>	
									<!-- <div class="selectcommodity_next_div" > -->
										<el-radio-group v-model="specificationRadio[index].name" 
											style="display: flex; flex-wrap: wrap;"  size="medium">
										<div v-for="obj2 in obj.commoditySpecificationNames" :key="obj2" class="mr-2">
											<el-radio :label="obj2.specificationname" border>{{obj2.specificationname}}</el-radio>
										</div>
								</el-radio-group>	
								<!-- </div> -->
							</div>
							
						<!-- 商品数量 -->
						<div class="selectcommodity">
							<div style="font-size: 12px;color: #72767B; width: 40px; margin-left: 1rem;" class="mt-2">数量</div>	
								<el-input-number size="small" v-model="num" :min="1" :max="10"></el-input-number>
						</div>
						
						<!-- 购买或加入购物车 -->
						<div >
						<hr style="border: 1px dashed #909399;" >	
						<div class=" d-flex">
							<el-button type="danger">立即购买</el-button>	
							<el-button type="warning" icon="el-icon-shopping-cart-2" @click="addshoppingtrolley" >加入购物车</el-button>
						</div>
						</div>
						
		</div>
		
	
		</div>
</template>

<script>
	import {Ajax} from '../../../assets/js/ajax.js'
	import bus from '../../../main.js'
	export default{
		name:'Commodityinfo',
		props:{
			commodity:Object,
		},
		data:function(){
			return{
				specificationRadio:[],
				num:1,
				specificationTitle:"",
				addspecification:"",
				user:""
			}
		},
		created:function(){
			var data="commodityId="+parseInt(this.$route.params.id);
			Ajax("post","commodityspecificationtitle/getcommdidtyId",data,(data)=>{
				this.specificationTitle=data;
				
				for(let i=0;i<this.specificationTitle.length;i++){
					//复选框默认选择第一个
					this.specificationRadio.push(
					{"name":this.specificationTitle[i].commoditySpecificationNames[0].specificationname}
					)
				}
				bus.$emit("commodityId",this.$route.params.id)
			})
		},
		methods:{
		skipcomment:function(){
				/* alert(this.specificationRadio[0].name)
				document.querySelector("#aaaa").scrollIntoView(true); */
				
				//跳转评论模块
				bus.$emit('commentDom',0);
			},
			addshoppingtrolley:function(){
				//加入购物车
				Ajax("get","user/homePageUser",null,(data)=>{
					this.user=data
					if(data!=""){
				this.addspecification="";
				/* alert(this.specificationRadio.length) */
				for(let i=0;i<this.specificationRadio.length;i++){
					if(i==0){
						this.addspecification+=this.specificationRadio[i].name+"，";
					}else{
					this.addspecification+=this.specificationRadio[i].name;	
					}
				}
				var dataAdd="commodityname="+this.commodity.commodityname+"&commodityspecification="+this.addspecification
				+"&commodityprice="+this.commodity.commodityprice+"&commoditycount="+this.num
				+"&totalprice="+(this.commodity.commodityprice*this.num)+"&uid="+parseInt(data.id)
				+"&commodityid="+parseInt(this.$route.params.id);
				Ajax("post","shoppingtrolley/addShoppingtrolley",dataAdd,(result)=>{
					if(result.success==true){
						alert(result.message)
					}
					if(result.success==false){
						alert(result.message)
					}
				})
				}else{
					alert("你还没有登录")
				}
				}) 
			}
		}
	}
</script>

