<template>
		<div class="commodityimg">
			
				<el-tabs :tab-position="tabPosition">
		<el-tab-pane v-for="obj in commodityImg" :key="obj" >
			<img slot="label" :src="require('@/assets/'+obj.imgurl)" style="width: 52px;" >	
		<img class="showCommodityImg container" :src="require('@/assets/'+obj.imgurl)">
		<!-- <img slot="label" src="../../../assets/imgs/mantrousers5.jpg"
					style="width: 52px;" >
		<img class="showCommodityImg" src="../../../assets/imgs/childrenwear6.jpg"> -->
		</el-tab-pane>
		</el-tabs>
		<div class="col" style="font-size: 13px;">

		<div style="float: left;" class="mt-1">
			<img style="cursor: pointer;" v-show="imgflag" @click="changs" src="../../../assets/imgs/heart_h.png" >			
			<img style="cursor: pointer;" v-show="!imgflag" @click="changs" src="../../../assets/imgs/heart.png" >			
		</div>
			<div style="float: left;" class="ml-2 mt-1"> 
				<div>收藏（已有{{commodity.collectcount}}人收藏）</div>
			</div>	
		</div>
		</div>
</template>

<script>
	import {Ajax} from '../../../assets/js/ajax.js'
	import bus from '../../../main.js'
	export default{
		name:'Commodityimg',
		data:function(){
			return{
				tabPosition: 'bottom',
				imgflag:false,
				commodityImg:[{
					imgurl:"imgs/womanshoes6.jpg"
				},
				{
					imgurl:"imgs/womanshoes6.jpg"
				},
				{
					imgurl:"imgs/womanshoes6.jpg"
				}],
				commodity:"",
				user:""
			}
		},
		methods:{
			changs:function(){
			Ajax("get","user/homePageUser",null,(data)=>{
				this.user=data
					if(data==""){
						alert("你还没右登录");
					} 
				if(data!=""){
					var datacollect="commodityId="+parseInt(this.$route.params.id);		
						this.imgflag=!this.imgflag
						//添加商品收藏
						if(this.imgflag){
							Ajax("post","commodity/addCommodityCollect",datacollect,(data)=>{
								if(datacollect==""){
									alert("收藏失败！");
								}
								if(datacollect!=""){
									alert("收藏成功！")
									this.commodity=data
								}
							})
						}
						//删除商品收藏
						if(!this.imgflag){
							Ajax("post","commodity/delCommodityCollect",datacollect,(data)=>{
								if(datacollect==""){
									alert("取消失败！");
								}
								if(datacollect!=""){
									alert("取消成功！")
									this.commodity=data
								}
							})
						}
					}
					}) 
			}
		},
		created:function(){
		//查询当前商品的图片
		bus.$on("commodityId",(value)=>{
			var data="commodityId="+parseInt(value);
			Ajax("post","commodity/findCommodityImgCommodityId",data,(data)=>{
				this.commodityImg=data
			})
		})
		//查询收藏人气
		var data="commodityId="+parseInt(this.$route.params.id);
		Ajax("post","commodity/querycommodityId",data,(data)=>{
			this.commodity=data
		})
		//查询当前用户是否收藏了当前产品
		Ajax("post","commodityCollect/findCommodityCollectUidAndcid",data,(data)=>{
			if(data==""){
				this.imgflag=false;
			} 
			if(data!=""){
			this.imgflag=true;
			}
		})
		
		}
	}
</script>

<style>
