<template>
	<div>
		<!-- 导航栏 -->
		<Navigation></Navigation>
	<div class="container">
		<SearchboxComponents></SearchboxComponents>
		<hr style="border:solid 1px #666666" >
		<div class="commodityinfo">
			<!-- 商品图片 -->
			<Commodityimg></Commodityimg>
				<!--商品详情信息 -->
				<Commodityinfo :commodity="commodity"></Commodityinfo>
				<!-- 商品详情右边图片 -->
				<CommodityRightimg></CommodityRightimg>
					</div>
					<!-- 评论组件 -->
					<br>
					<CommodityCommentDom></CommodityCommentDom>
		</div>
	</div>
</template>
<script>
	import Commodityinfo from '../components/commodity/commoditydetails/Commodityinfo.vue'
	import Commodityimg from '../components/commodity/commoditydetails/Commodityimg.vue'
	import Navigation from '../components/Navigation.vue'
	import{Ajax} from '../assets/js/ajax.js'
	import SearchboxComponents from '../components/SearchboxComponents.vue'
	import CommodityRightimg from'../components/commodity/commoditydetails/CommodityRightimg.vue'
	import CommodityCommentDom from "../components/commodity/commoditydetails/CommodityCommentDom.vue"
	export default{
		name:'CommoditydetailedPage',
		components:{
			SearchboxComponents,
			Commodityinfo,
			Commodityimg,
			Navigation,
			CommodityRightimg,
			CommodityCommentDom
		},
		data() {
      return {
        isCollapse: true,
				tabPosition: 'bottom',
				commodity:"",
				commodityId:0
      };
    },
		created:function(){
			var data="commodityId="+parseInt(this.$route.params.id);
			Ajax("post","commodity/querycommodityId",data,(data)=>{
				this.commodity=data
			})
		}/* ,
		watch:{
			$route:{
				handler:function(to,from){
					console.log(to)
					console.log(from)
					var data="commodityId="+parseInt(this.$route.params.id);
					Ajax("post","commodity/querycommodityId",data,(data)=>{
						this.commodity=data
					})
				},
				deep:true
		}
	} */
	}
</script>