<!-- 评论组件 -->
<template>
	<div id="aaaa">
		
		<div class="d-flex container align-items-center" style="height:50px;border: 1px solid #D9D9D9;">
				<el-tabs v-model="activeName">
						<el-tab-pane label="评价" name="comment"/>
						<el-tab-pane label="商品详情" name="commoditydetails"/>
					</el-tabs>
		</div>
		
		<div style="background-color: white;"  class="mt-2">
			<!-- 标题 --> 
			<div class="d-flex align-items-center container"
			style="background-color:#DFE4ED; height: 40px;border: 1px solid #D9D9D9;">
				<div style="font-weight: bold;">商品评价</div>
			</div>
				
				<!-- 商品评论模板 -->
			<template>
				<template v-if="commoditycomments!=null||commoditycomments!=''">
					<div v-for="(v,index) in commoditycomments" :key="v"
					class=" mt-3 d-flex container align-items-start" style="border-bottom:0.5px solid #CCCCCC;color: #000000;">
							<div class="d-flex  mb-3 col-2 align-items-center">
								<img src="../../../assets/imgs/1/mantrousers1.jpg" width="30px" height="30px" style="border-radius: 50%;" >
								<span class="ml-2" style="font-size: 12.5px;color: #606266; text-align: left;">{{v.user.username}}</span>
							</div>
							<div class="col" style="text-align: left;">
									<div style="font-size: 14px;">
										{{v.commentmessage}}
									</div>
								<!-- <div>	<img src="../../../assets/imgs/1/mantrousers1.jpg" width="120px" ></div> -->
								<p class=" operation col-3 d-flex justify-content-around" style="float: right;">
									<span style="font-size: 13px;color: #606266;">
<!-- 								{{v.commentdate.length>10?v.commentdate.substring(0,10)+"":v.commentdate}}
 -->									</span>
									<!-- <template v-if="user!=''" > -->
									<template>
										<i v-if="user.id==v.user.id" @click="delCommodityComment(v.id)" class="el-icon-delete"></i>
										<i class="el-icon-chat-dot-round"></i>
									</template>
								</p>
								<!-- 回复评论组件 -->
								<template v-if="v.commodityCommentReplies!='' && replybooleans!=''" >
								<CommodityCommentReply :commodityCommentRepliesObj="v.commodityCommentReplies"
								:replybooleans="replybooleans[index].replyString" :index="index"></CommodityCommentReply>
								</template>
								<!-- <div v-if="v.commodityCommentReplies!='' && replybooleans!=''">
									<div class="mt-1" v-show="replybooleans[index].replyString" v-for="commentreplyobj in v.commodityCommentReplies" :key="commentreplyobj">
										<div class="d-flex align-items-center">
												<img src="../../../assets/imgs/1/mantrousers1.jpg" width="24px" height="24px" style="border-radius: 50%;" >
												<span class="ml-2 " style="font-size: 10.5px;color: #606266; text-align: left;">{{commentreplyobj.user.username}}
												<i class="el-icon-caret-right"></i></span>
										</div>
										<div style="font-size: 12px;"  class="container ml-3 col-8">
												{{commentreplyobj.commentreply}}
										</div>
									</div>
									<span @click="replybooleans[index].replyString=!replybooleans[index].replyString"
									style="font-size: 10px; color: #82848A; cursor: pointer;">
									<span v-if="replybooleans[index].replyString==false">展开{{v.commodityCommentReplies.length}}条回复<i class="el-icon-arrow-down"></i></span>
									<span v-if="replybooleans[index].replyString==true">收起回复<i class="el-icon-arrow-up"></i></span>
									</span>
								</div> -->
								
							</div>
						</div>
				</template>
					<h4 class="mt-3 mb-3" v-if="commoditycomments==''">此商品暂时没有任何评价！！！</h4>
			</template>
		</div>
		
	</div>
</template>

<script>
	import bus from '../../../main.js'
	import {Ajax} from '../../../assets/js/ajax.js'
	import CommodityCommentReply from "./reply/CommodityCommentReply.vue"
	export default{
		name:"CommodityCommentDom",
		components:{
			CommodityCommentReply
		},
		data:function(){
			return{
				user:'',
				activeName:"comment",
				commoditycomments:[
					{id:0,commentmessage:"非常好",user:{username:"梁文航"},
					url:require('../../../assets/imgs/1/mantrousers1.jpg')},
					{id:0,commentmessage:"非常好",user:{username:"支付"},
					url:require('../../../assets/imgs/1/mantrousers1.jpg')}
				],
				commoditycommentreplys:[
					{id:0,commentreply:"不错吗？"},
					{id:0,commentreply:"不错吗？"},
					{id:0,commentreply:"不错吗？"},
					{id:0,commentreply:"不错吗？"}
				],
				replybooleans:''
			}
		},
		created:function(){
			//查询当前用户
			Ajax("get","user/homePageUser",null,(data)=>{
				this.user=data
			}) 
			this.queryCommentCommodityIdAll(-1);//查询所有评论信息	
			
			//跳转评论模块
			bus.$on("commentDom",(value)=>{
				console.log(value)
			document.querySelector("#aaaa").scrollIntoView(true);
			})
			
			//回复评论——子组件传递过来的带的值没有任何用
			bus.$on("commodityComment",(value)=>{
				console.log(value)
				//value是回复评论的索引下标
				this.queryCommentCommodityIdAll(value)
			})
		},
		methods:{
			//删除商品
			delCommodityComment:function(id){
				this.$confirm('此操作将永久删除该评论, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
					var dateComment="id="+id;
					Ajax("post","commoditycomment/delCommodityComment",dateComment,(result)=>{
							if(result.success==true){
								this.$message({
								type: 'success',
								message: result.message
								});
								this.queryCommentCommodityIdAll(-1);//查询所有当前商品的所有评论信息
							}else{
								alert(result.message)
							}
					})
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
			},
			//查询评论信息
			queryCommentCommodityIdAll:function(index){
				//查询评论信息
				var data="commodityId="+parseInt(this.$route.params.id);
				Ajax("post","commoditycomment/queryCommentCommodityId",data,(dataResult)=>{
					this.commoditycomments=dataResult
					console.log(this.commoditycomments)
					this.replybooleans=''
					
					//解决展开重复问题
					this.replybooleans=[]
					for(var i=0;i<dataResult.length;i++){
						if(dataResult[i].commodityCommentReplies!=''){
							this.replybooleans.push({"replyString":false})
							console.log(this.replybooleans)
						}
					}
					//传-1是因为Boolean值传到子组件默认为false
					if(index!=-1){
						this.replybooleans[index].replyString=true;
					}
				})
			}
		}
	}
</script>

<style>
.operation .el-icon-delete,.el-icon-chat-dot-round{
		cursor: pointer; 
	}
</style>
